}

.btn-hero:active {
  color: #000;
}

.btn-hero:active:after {
  background: none;
}

.btn-hero:hover:before {
  opacity: 1;
}

.btn-hero:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

