.btn-hero {
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: $primary-text-color;
  background: $prinmary-bg-color;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  font-weight: 600;

  transition: all 250ms linear;

  &:hover {
    scale: 1.05;
  }
}

.btn-hero:before {
  content: '';
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing_54134 20s linear infinite;
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.btn-hero:active {
  color: #000;
}

.btn-hero:active:after {
  background: transparent;
}

.btn-hero:hover:before {
  opacity: 1;
}

.btn-hero:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@keyframes glowing_54134 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.btn {
  font-weight: 500;
  padding: 8px 16px;
  border: none;
  color: $primary-text-color;
  background: $acent-color;
  margin-top: 30px;
  border-radius: 4px;

  transition: all 0.5s ease;

  &:hover,
  &:focus {
    color: $acent-color;
    background: $primary-text-color;
  }
}
